<template>
    <div>
        <NavHead></NavHead>
        <div class="book-shelf" :class="{ empty: !isHaveBook }">
            <ul v-if="isHaveBook" class="shelf-books">
                <li
                    class="shelf-item"
                    v-for="(item, index) in books"
                    :key="index"
                    @click.stop="routeTo(item._id)"
                >
                    <div class="img">
                        <img
                            :src="
                                'http://statics.zhuishushenqi.com' + item.cover
                            "
                        />
                    </div>
                    <div class="text">
                        <div class="text-title">
                            <span class="title">{{ item.title }}</span>
                            <span @click.stop="delBook(index, item._id)"
                                ><van-icon name="delete"
                            /></span>
                        </div>
                        <div class="text-chapter">
                            <span class="time">{{ item.updated }}</span>
                            <span class="chapter">{{ item.lastChapter }}</span>
                        </div>
                    </div>
                </li>
            </ul>
            <div v-else class="empty-shelf">
                <img src="../assets/emptyShelf.svg" />
                <span class="">书架暂无书籍</span>
            </div>
        </div>
    </div>
</template>

<script>
import NavHead from "@/components/NavHead.vue";
import store from "@/store/index.js";
import { getData, TimeDifference } from "@/api/api.js";
export default {
    name: "Bookshelf",
    components: {
        NavHead,
    },
    data() {
        return {
            isHaveBook: false,
            books: [],
        };
    },
    methods: {
        delBook(index, id) {
            this.books.splice(index, 1);
            let bookshelf = JSON.parse(localStorage.getItem("bookshelf"));
            bookshelf.data["book-" + id] = undefined;
            localStorage.setItem("bookshelf", JSON.stringify(bookshelf));
        },
        routeTo(id) {
            this.$router.push({ name: "BookDetail", params: { id: id } });
        },
    },
    watch: {
        books(val){
            if(val.length == 0){
                this.isHaveBook = false;
            } else {
                this.isHaveBook = true;
            }
        },
    },
    beforeRouteEnter(to, from, next) {
        let bookshelf = JSON.parse(localStorage.getItem("bookshelf"));
        if (bookshelf && Object.keys(bookshelf.data).length != 0) {
            store.state.loading = true;
            let books = [];
            for (let key in bookshelf.data) {
                let id = bookshelf.data[key].id;
                getData(
                    "https://api.kele8.cn/agent/http://novel.kele8.cn/book-info/" +
                        id
                )
                    .then((res) => {
                        res.data.updated = TimeDifference(res.data.updated);
                        books.push(res.data);
                    })
                    .catch((err) => {
                        console.log(err);
                        store.state.loading = false;
                    });
            }
            store.state.loading = false;
            next((vm) => {
                vm.isHaveBook = true;
                vm.books = books;
            });
        } else {
            next((vm) => {
                vm.isHaveBook = false;
            });
        }
    },
};
</script>

<style lang="less" scoped>
.book-shelf {
    display: flex;
    height: 84vh;
    max-height: 84vh;
    max-width: 100vw;
    overflow-x: hidden;
    overflow-y: scroll;
    &.empty {
        align-items: center;
        justify-content: center;
    }
    .shelf-books {
        padding: 10px;
        .shelf-item {
            margin-top: 10px;
            display: flex;
            .img {
                flex: 2;
                img {
                    width: 100%;
                }
            }
            .text {
                flex: 10;
                margin-left: 10px;
                padding: 10px 0;
                .text-title {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-top: 5px;
                }
                .text-chapter {
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    font-size: 13px;
                    text-align: left;
                    margin-top: 5px;
                    .time {
                        flex: 3;
                        white-space: nowrap;
                    }
                    .chapter {
                        flex: 9;
                        max-width: 60vw;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                }
            }
        }
    }
    .empty-shelf {
        display: flex;
        flex-direction: column;
        img {
            width: 120px;
        }
    }
}
</style>
